
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
}
a {
  color: inherit;
}
body {
  margin: 0;
  padding: 0;
}
.content {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding-bottom: 60px;
}
.container {
  max-width: 1080px;
  margin: 0 auto;
  padding: 0 40px;
}
.films {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 30px;
}
.films__item {
    width: 25%;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.pagination__item a {
    padding: 5px 10px;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    margin: 0 5px;
}
.pagination__item a:hover {
      -webkit-box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.2);
              box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.pagination__item.active a {
    background-color: #0874ff;
    color: #fff;
}
.star {
  position: relative;
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: .9em;
  margin-right: .9em;
  margin-bottom: .8em;
  border-right: .3em solid transparent;
  border-bottom: .7em  solid #FC0;
  border-left: .3em solid transparent;
  /* Controlls the size of the stars. */
  font-size: 10px;
}
.star:before, .star:after {
    content: '';
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    top: .6em;
    left: -1em;
    border-right: 1em solid transparent;
    border-bottom: .7em  solid #FC0;
    border-left: 1em solid transparent;
    -webkit-transform: rotate(-35deg);
            transform: rotate(-35deg);
}
.star:after {
    -webkit-transform: rotate(35deg);
            transform: rotate(35deg);
}

.header {
  z-index: 1;
  position: relative;
  padding: 30px 0;
}
.header__inner {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.logo {
  font-size: 24px;
  text-decoration: none;
}
.search {
  position: relative;
  width: 400px;
}
.search__input {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    font-size: 16px;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.1);
}
.search__results {
    position: absolute;
    top: 100%;
    width: 100%;
    left: 0;
    max-height: 300px;
    overflow: auto;
    background-color: #fff;
    padding: 15px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 0 0 5px 5px;
}
.search__item {
    padding: 5px 10px;
}
.search__item:hover {
      background-color: rgba(0, 0, 0, 0.22);
}
.search__item .item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.search__item .item__poster {
        width: 50px;
        padding-top: 20%;
        margin-right: 10px;
        margin-bottom: 0;
}
.search__item .item__poster img {
          width: 100%;
}

.item {
  text-decoration: none;
}
.item:hover .item__title {
    color: #0874ff;
}
.item:hover .item__poster img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.item__title {
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.item__favorite {
    position: absolute;
    top: 10px;
    right: 0;
    background-color: #0874ff;
    color: white;
    padding: 5px;
}
.item__poster {
    overflow: hidden;
    position: relative;
    width: 100%;
    padding-top: 150%;
    margin-bottom: 15px;
}
.item__poster img {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      -o-object-fit: cover;
         object-fit: cover;
}

.footer {
  background-color: #0874ff;
  color: #fff;
  padding: 40px 0;
}

.film__row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 50px;
}
.film__col {
  width: 50%;
}
.film__favorite {
  position: absolute;
  top: 10px;
  right: 0;
  background-color: #0874ff;
  color: white;
  padding: 5px;
}
.film__poster-container {
  margin-right: 50px;
}
.film__poster {
  position: relative;
  max-width: 400px;
  margin: 0 auto;
}
.film__poster img {
    width: 100%;
}
.film__genre {
  position: relative;
}
.film__genre:hover {
    text-decoration: none;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color: #0874ff;
}
.film__genre:not(:last-child) {
    padding-right: 5px;
    margin-right: 5px;
}
.film__genre:not(:last-child):after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 1px;
      background-color: #989588;
}
.film__btn {
  margin-top: 20px;
}
.film__btn button {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    font-size: 10px;
    text-transform: uppercase;
    padding: 10px;
    background-color: transparent;
    cursor: pointer;
    border: 2px solid #0874ff;
    border-radius: 3px;
    font-weight: bold;
    color: #0874ff;
}
.film__btn button:hover {
      background-color: #0874ff;
      color: #fff;
}
.film__btn button.remove {
      border-color: #ff4f51;
      color: #ff4f51;
}
.film__btn button.remove:hover {
        background-color: #ff4f51;
        color: #fff;
}
.similar-films__title {
  font-size: 20px;
}
.similar-films__item {
  padding: 10px;
  width: 150px;
}
.similar-films__item img {
    width: 100%;
}




















































































































